/* Define CSS custom properties (variables) */
:root {
    --bg-color:#1f242d;
    --second-bg-color:#323946;
    --text-color:#fff;
    --main-color: #0ef;
}

/* .about selector */
.about {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 2rem;
    padding: 20px;
    background: var(--second-bg-color);
}

/* .about-img selector */
.about-img img {
    width: 35vw;
}

/* .heading selector */
.heading {
    text-align: center;
    font-size: 2.5rem;
}

/* .about-content h2 selector */
.about-content h2 {
    text-align: left;
    line-height: 1.2;
}

/* .about-content h3 selector */
.about-content h3 {
    font-size: 25px;
    color: white;
}

/* .about-content p selector */
.about-content p {
    font-size:20px;
    margin: 2rem 0 3rem;
    color: white;
}
