/* Define CSS custom properties (variables) */
:root {
    --bg-color: #1f242d;
    --text-color: #fff;
    --main-color: #0ef;
}

/* .header selector */
.header {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 60px;
    padding: 10px;
    /* padding: 2rem 9%; */
    background: var(--bg-color);
    display: flex;
    justify-content: space-between;
    align-items: center;
    z-index: 100;
}

/* .header .sticky selector */
.header.sticky {
    border-bottom: 1rem solid rgba(0, 0, 0, 0.1);
}

/* .logo selector */
.logo {
    font-size: 1.5rem;
    color: var(--text-color);
    font-weight: 600;
    cursor: default;
}

.navbar {
    
    margin-right: 30px;
}
/* .navbar a selector */
.navbar a {
    font-size: 20px;
    color: var(--text-color);
    margin-left: 30px;
}

/* .navbar a:hover and .navbar a.active selectors */
.navbar a:hover,
.navbar a.active {
    color: var(--main-color);
}

/* #menu-icon selector */
#menu-icon {
    font-size: 1.6rem;
    color: var(--text-color);
    display: none;
}
