/* Define CSS custom properties (variables) */

:root {
    --bg-color:#1f242d;
    --second-bg-color:#323946;
    --text-color:#fff;
    --main-color: #0ef;
}


/* Keyframes animation */
@keyframes floatImage {
    0% {
        transform: translateY(0);
    }
    50% {
        transform: translateY(-2.4rem);
    }
    100% {
        transform: translateY(0);
    }
}

/* .home selector */
.home {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 20px;
    margin-top: 50px;
    background-color: #1f242d;
}

/* .home-img selector */
.home-img img {
    width: 35vw;
    animation: floatImage 4s ease-in-out infinite;
}

/* .home-content selector */
.home-content h3 {
    font-size: 3.2rem;
    font-weight: 700;
    color: #0ef;
}
.home-content {
    text-align: left;
}
.home-content h3:nth-of-type(2) {
    margin-bottom: 2rem;
}

/* Select spans within .home-content */
.home-content span {
    color: #0ef;
}

.home-content h1 {
    font-size: 3.6rem;
    font-weight: 700;
    line-height: 1.3;
    color: white;
}

.home-content p {
    font-size: 20px;
    color: white;
    width: 700px;
}

/* .social-media selector */
.social-media a {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    width: 4rem;
    height: 4rem;
    background: transparent;
    border: .2rem solid var(--main-color);
    border-radius: 50%;
    font-size: 2rem;
    color: var(--main-color);
    margin: 3rem 1.5rem 3rem 0;
    transition: 0.5s ease;
}

.social-media a:hover {
    background-color: var(--main-color);
    color: var(--second-bg-color);
    box-shadow: 0 0 1rem var(--main-color);
}

/* .btn selector */
.btn {
    display: inline-block;
    padding: 1rem 2.8rem;
    background: var(--main-color);
    border-radius: 4rem;
    box-shadow: 0 0 1rem var(--main-color);
    font-size: 1.6rem;
    color: var(--second-bg-color);
    letter-spacing: 0.1rem;
    font-weight: 600;
    transition: 0.5s ease;
}

.btn:hover {
    box-shadow: none;
}
