/* Define CSS custom properties (variables) */
:root {
    --bg-color:#1f242d;
    --second-bg-color:#323946;
    --text-color:#fff;
    --main-color: #0ef;
}

.contact{
    background-color: #1f242d;
    padding: 20px;
}
/* .contact h2 selector */
.contact h2 {
    margin-bottom: 3rem;
}

/* .contact form selector */
.contact form {
    max-width: 70rem;
    margin: 1rem auto;
    text-align: center;
    margin-bottom: 3rem;
}

/* .contact form .input-box selector */
.contact form .input-box {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}

/* .contact form .input-box input and .contact form textarea selectors */
.contact form .input-box input,
.contact form textarea {
    width: 100%;
    padding: 20px;
    font-size: 16px;
    color: var(--text-color);
    background: var(--second-bg-color);
    border-radius: 0.8rem;
    margin: 0.7rem 0;
}

/* .contact form .input-box input selector */
.contact form .input-box input {
    width: 100;
}

/* .contact form textarea selector */
.contact form textarea {
    resize: none;
}

/* .contact form .btn selector */
.contact form .btn {
    margin-top: 2rem;
    cursor: pointer;
}
