/* Define CSS custom properties (variables) */
:root {
    --bg-color:#1f242d;
    --second-bg-color:#323946;
    --text-color:#fff;
    --main-color: #0ef;
}

/* .footer selector */
.footer {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    padding: 0 20px;
    background: var(--second-bg-color);
}

/* .footer-text p selector */
.footer-text p {
    font-size: 20px;
}

/* .footer-iconTop a selector */
.footer-iconTop a {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    padding: 0.8rem;
    background: var(--main-color);
    border-radius: 0.8rem;
    transition: 0.5s ease;
}

/* .footer-iconTop a:hover selector */
.footer-iconTop a:hover {
    box-shadow: 0 0 1rem var(--main-color);
}

/* .footer-iconTop a i selector */
.footer-iconTop a i {
    font-size: 2.4rem;
    color: var(--second-bg-color);
}
