/* Define CSS custom properties (variables) */
:root {
    --bg-color:#1f242d;
    --second-bg-color:#323946;
    --text-color:#fff;
    --main-color: #0ef;
}

/* .portfolio selector */
.portfolio {
    background: var(--second-bg-color);
    padding: 20px;
}

/* .portfolio h2 selector */
.portfolio h2 {
    margin-bottom: 4rem;
}

/* .portfolio-container selector */
.portfolio-container {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    align-items: center;
    gap: 2.5rem;
}

/* .portfolio-container .portfolio-box selector */
.portfolio-container .portfolio-box {
    position: relative;
    border-radius: 2rem;
    box-shadow: 0 0 1rem var(--bg-color);
    overflow: hidden;
    display: flex;
}

/* .portfolio-box img selector */
.portfolio-box img {
    width: 100%;
    transition: 0.5s ease;
}

/* .portfolio-box:hover img selector */
.portfolio-box:hover img {
    transform: scale(1.1);
}

/* .portfolio-box .portfolio-layer selector */
.portfolio-box .portfolio-layer {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(rgba(0, 0, 0, 0.1), var(--main-color));
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    text-align: center;
    padding: 0 4rem;
    transform: translateY(100%);
    transition: 0.5s ease;
}

/* .portfolio-box:hover .portfolio-layer selector */
.portfolio-box:hover .portfolio-layer {
    transform: translateY(0);
}

/* .portfolio-layer h4 selector */
.portfolio-layer h4 {
    font-size: 3rem;
}

/* .portfolio-layer p selector */
.portfolio-layer p {
    font-size: 1.6rem;
    margin: 0.3rem 0 1rem;
}

/* .portfolio-layer a selector */
.portfolio-layer a {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    width: 5rem;
    height: 5rem;
    background: var(--text-color);
    border-radius: 50%;
}

/* .portfolio-layer a i selector */
.portfolio-layer a i {
    font-size: 2rem;
    color: var(--second-bg-color);
}
