/* Define CSS custom properties (variables) */
:root {
    --bg-color:#1f242d;
    --second-bg-color:#323946;
    --text-color:#fff;
    --main-color: #0ef;
}

.services{
    background-color: #1f242d;
    padding: 20px;
}
/* .services h2 selector */
.services h2 {
    margin-bottom: 5rem;
    
}

/* .services-container selector */
.services-container {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    gap: 2rem;
}

/* .services-container .services-box selector */
.services-container .services-box {
    flex: 1 1 30rem;
    background: var(--second-bg-color);
    padding: 3rem 2rem 4rem;
    border-radius: 2rem;
    text-align: center;
    border: 0.2rem solid var(--bg-color);
    transition: 0.5s ease;
}

/* .services-container .services-box:hover selector */
.services-container .services-box:hover {
    border-color: var(--main-color);
    transform: scale(1.02);
}

/* .services-box i selector */
.services-box i {
    font-size: 7rem;
    color: var(--main-color);
    text-decoration: none;
}

/* .services-box h3 selector */
.services-box h3 {
    font-size: 1.6rem;
}

/* .services-box p selector */
.services-box p {
    font-size: 20px;
    margin: 1rem 0 3rem;
    color: white;
}
